// 類似商品、推薦給你
.sec-pd.pd-list {
	.pd-card {
		$root: &;

		padding: 0;

		&.grid-x {
			margin: 0;
		}

		&__item {
			max-width: rem-calc(110);
		}
	}

	@include breakpoint(small only) {
		.pd-card--scroll.grid-x {
			margin-right: rem-calc(-20);
			margin-left: rem-calc(-20);
			padding-right: rem-calc(20);
			padding-left: rem-calc(20);
		}

		.pd-card__btn-fav {
			top: rem-calc(4);
			right: rem-calc(4);
		}
	}

	@include breakpoint(medium down) {
		.pd-card.grid-x {
			> .pd-card__item.cell {
				margin-left: 0;
				margin-right: rem-calc(17);
				min-width: calc(100% / 6 - 85px / 6);

				&:last-child {
					margin-right: 0;
				}
			}
		}
	}

	@include breakpoint(medium) {
		// 調整標題大小
		// 2021/12/26 因為P1已經拿去套版很久，避免改動到html，以CSS方式更改
		.sec-title-box__title {
			font-size: rem-calc(20);
		}
	}

	@include breakpoint(large) {
		.pd-card.grid-x {
			> .pd-card__item.cell {
				margin-left: 0;
				margin-right: rem-calc(20);
				min-width: calc(100% / 6 - 100px / 6);

				&:last-child {
					margin-right: 0;
				}
			}
		}

		.pd-list__cont {
			padding: 0;
		}
	}
}

// --------------------------------------------------------

// 相關產品
.pd-bottom-container {
	@include breakpoint(medium down) {
		padding: 0;
		background-color: $white;

		.pd-list {
			background-color: $white;
		}
	}

	// 處理桌機
	@include breakpoint(large) {
		margin-top: rem-calc(103);
	}
}
