.count-box {
	$root: &;

	&__input-box {
		display: flex;
		// align-items: center;
		justify-content: center;
		border: 1px solid #ddd;
		border-radius: rem-calc(6);

		&:focus-within {
			border-color: $primary-color;
			box-shadow: 0 0 0 rem-calc(1) $primary-color inset; // 加粗，畫面不會跳動的方法
		}
	}

	&__btn {
		@include selection-none;

		position: relative;
		margin-bottom: 0;
		min-width: 0;
		width: 4.533em;
		font-size: rem-calc(12);

		&,
		&:hover,
		&:focus {
			background: transparent;
			color: #999;
		}

		&.disabled,
		&[disabled] {
			&,
			&:hover,
			&:focus {
				color: #ddd;
				background: transparent;
			}
		}

		@at-root %count-sign {
			$width: 18;
			$height: round(2 / $width * 100) / 100 * 1em;

			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			margin-top: $height * -1 / 2;
			margin-left: -0.5em;
			width: 1em;
			height: $height;
			min-height: 2px;
			background: currentColor;
		}

		// 減
		&.minus {
			&::before {
				@extend %count-sign;
			}
		}

		&.plus {
			&::before {
				@extend %count-sign;
			}

			&::after {
				@extend %count-sign;

				transform: rotate(90deg);
			}
		}
	}

	&__input {
		// 隱藏 Default Arrow: Chrome, Safari, Edge, Opera
		&::-webkit-outer-spin-button,
		&::-webkit-inner-spin-button {
			-webkit-appearance: none;
		}

		// 隱藏 Default Arrow: Firefox
		&[type='number'] {
			-moz-appearance: textfield;
		}

		margin-bottom: 0;
		padding-left: 0;
		padding-right: 0;
		text-align: center;

		&,
		&:focus {
			border: 0 none;
			box-shadow: none;
			background: transparent;
		}
	}

	&__tip {
		margin-top: rem-calc(4);
		font-size: rem-calc(12);
		text-align: right;
		line-height: 1.29;
		color: $bfSecondaryA;

		@include breakpoint(medium) {
			font-size: rem-calc(14);
		}
	}
}
