// 【固定在下方】商品價格 + 商品
.pd-fixed-info {
	$root: &;

	position: fixed;
	z-index: 500;
	bottom: 0;
	left: 0;
	width: 100%;
	background: $white;
	border-top-left-radius: rem-calc(6);
	border-top-right-radius: rem-calc(6);
	box-shadow: $shadow-panel-bottom;

	&__cont {
		--padding-y: #{rem-calc(10)};

		display: flex;
		justify-content: space-between;
		padding-top: var(--padding-y);
		padding-bottom: var(--padding-y);
	}

	// 價格幣值
	&__currency {
		font-size: rem-calc(12);
		font-weight: 500;
		line-height: 1.2;
		color: $title-dark;
	}

	&__price-box {
		@include breakpoint(large) {
			position: relative;

			&.coin {
				#{$root} {
					&__price {
						padding-bottom: rem-calc(20);
					}

					&__note {
						position: absolute;
						bottom: 0;
						right: rem-calc(16);
						white-space: nowrap;

						@include breakpoint(xlarge) {
							right: rem-calc(24);
						}
					}
				}
			}
		}
	}

	// 價格
	&__price {
		font-size: rem-calc(20);
		font-weight: 600;
		line-height: 1.2;
		white-space: nowrap;
		color: $primary-orange;

		~ #{$root} {
			&__note {
				margin-top: rem-calc(4);
			}
		}
	}

	// 按鈕
	&__button {
		margin-bottom: 0;
		min-width: rem-calc(130);

		@include breakpoint($xs) {
			min-width: rem-calc(150);
		}
	}

	// 撐【固定在下方】的高度
	&-space {
		height: rem-calc(74);

		&.medium {
			height: rem-calc(108);
		}

		&.large {
			height: rem-calc(140);
		}
	}

	// 數量提醒
	&__count-alert {
		&.mo {
			border-bottom-right-radius: 0;
			border-bottom-left-radius: 0;

			@include breakpoint(large) {
				display: none;
			}
		}
		// &.pc {
		// 	margin-right: rem-calc(16);
		// 	font-size: rem-calc(12);
		// 	white-space: nowrap;

		// 	@include breakpoint(medium down) {
		// 		display: none;
		// 	}

		// 	@include breakpoint(large) {
		// 		~ #{$root}__price-box.coin {
		// 			margin-left: rem-calc(16);
		// 		}
		// 	}
		// }
	}

	&.no-shadow {
		box-shadow: none;
	}
}
