// Main Style
@import 'layout/variable_mixin_function.scss';
// --------------------------------------------------------

/* Component */
@import './components/mem-user-info';
@import './components/pd-banner';
@import './components/pd-history';
@import './components/pd-ask';
@import './components/pd-detail';
@import './components/pd-seller-info';
@import './components/pd-fixed-info';
@import './components/pd-list-sec';
@import './components/dropdown-menu';
@import './components/list-small-note';
@import './components/cart-count-box';

.sec-pd {
	@include pd-sec;
}

%pd-sec-title {
	.sec-title-box {
		@include breakpoint(medium) {
			margin-bottom: rem-calc(24);
		}
	}
}

// --------------------------------------------------------

:root {
	--bg-color: #{$content-bg-color};
}

// --------------------------------------------------------

// Header Height
$index-hd-pc-height: #{rem-calc(131)};
$index-hd-mb-height: #{rem-calc(56)};

// 往下推出 fixed nav 高度
.hd-pd-cont {
	& ~ .ft {
		@include breakpoint(medium down) {
			margin-bottom: rem-calc(50 + 30);
		}
	}

	&.c2c ~ .ft {
		@include breakpoint(medium down) {
			margin-bottom: rem-calc(92);
		}
	}
}

// 主要內容
.pd-cont-container {
	position: relative;

	@include breakpoint(medium down) {
		margin-top: rem-calc(56);
		margin-bottom: 0;
		padding-top: 0;
	}

	@include breakpoint(small down) {
		.breadcrumbs {
			margin-bottom: 0;

			&-nav {
				display: flex;
				align-items: center;
				justify-content: space-between;
				position: absolute;
				z-index: 5;
				top: 0;
				left: 0;
				width: 100%;
				padding: rem-calc(17 20);
			}
		}

		.breadcrumbs-wrapper {
			margin-bottom: 0;
		}

		.pd-ctrls {
			display: flex;
			align-items: center;
			justify-content: center;

			.pd-card__btn-fav,
			.pd-card__btn-share {
				width: rem-calc(36);
				height: rem-calc(36);
			}

			.pd-card__btn-fav {
				position: relative;
				margin-top: 0;
				margin-left: rem-calc(10);
			}

			.be-icon {
				font-size: rem-calc(24);
			}
		}
	}

	@include breakpoint($xs-down) {
		.pd-ctrls {
			.pd-card__btn-fav {
				margin-left: rem-calc(5);
			}
		}
	}
}

.pd-cont-left {
}

.pd-cont-right {
	@include breakpoint(large) {
		padding-left: 1.5rem;
	}

	@include breakpoint(xlarge) {
		padding-left: 2.5rem;
	}
}

// --------------------------------------------------------

// PC 下滑出現 1024以上
.hd-main {
}

.pd-fixed-info {
	$root: &;

	&__cont {
		@include breakpoint(medium down) {
			align-items: center;
			width: 100%;
		}

		#{$root} & {
			~ .pd-card {
				margin-bottom: rem-calc(10);

				.pd-fixed-info__note {
					margin-top: 0;
				}
			}
		}
	}

	&__note {
		margin-top: rem-calc(5);
		margin-bottom: 0;
		font-size: rem-calc(12);
		color: $bfDarkGray;

		@include breakpoint(medium down) {
			margin-top: rem-calc(10);
		}

		em {
			font-style: normal;
			color: $bfSecondaryA;
		}
	}

	.pd-card {
		margin-left: 0;
		@include breakpoint(medium down) {
			margin-bottom: 0;
			&__item {
				margin-bottom: 0;
			}
		}
	}

	// 處理桌機
	@include breakpoint(large) {
		position: fixed;
		z-index: 999;
		top: rem-calc(75);
		bottom: auto;
		width: 100%;
		pointer-events: none;
		transform: translateY(-30px);
		opacity: 0;
		transition: 0.36s all;
		box-shadow: $shadow-panel;

		&.is-fixed {
			pointer-events: all;
			transform: translateY(0px);
			opacity: 1;
		}

		&__top,
		&__bottom {
			position: relative;
			width: 100%;
			border-top: rem-calc(1) solid #f4f4f4;
			background: $white;
		}

		&__top {
			display: flex;
			align-items: center;
			justify-content: space-between;
			z-index: 2;

			> .grid-container {
				width: 100%;
				flex-wrap: nowrap;
			}
		}

		&__bottom {
			z-index: 1;
		}

		// 價格
		&__cont {
			padding: 0;
			box-shadow: none;
			align-items: center;
		}

		&__price-box {
			display: flex;
			align-items: baseline;
			justify-content: center;
		}

		&__button-box {
			display: flex;
			align-items: center;
			justify-content: center;
		}

		&__price {
			margin: rem-calc(0 16);
		}

		.pd-card__img-tag {
			position: relative;
			top: auto;
			margin: 0;
			margin-left: rem-calc(5);
		}

		// 產品
		.pd-card {
			margin-bottom: 0;
			width: calc(100% - 410px);

			&__item {
				display: flex;
				align-items: center;
				justify-content: flex-start;
				margin-bottom: 0;
				padding: rem-calc(10) 0;
			}

			&__img-box {
				margin-right: rem-calc(15);
				margin-bottom: 0;
				width: rem-calc(60);
				border: rem-calc(1) solid $bfLightGray;
			}

			&__title {
				margin-bottom: 0;
			}

			&__info-box {
				max-width: calc(100% - 85px);
			}
		}

		// 頁籤
		.page-nav {
			&__item {
				max-width: rem-calc(148);
			}
		}
	}

	@include breakpoint(xlarge) {
		&__price {
			margin: rem-calc(0 24 0 20);
		}

		&__note {
			white-space: nowrap;
		}

		.pd-card__img-tag {
			margin-left: rem-calc(15);
		}
	}
}

// --------------------------------------------------------
// PC 左側

// 上方主要產品內容
.pd-top-container {
	// 處理手機
	@include breakpoint(medium down) {
		padding-left: 0;
		padding-right: 0;
	}
}

// TOP
.pd-top-info {
	@include breakpoint(small down) {
		padding-left: 0;
		padding-right: 0;
	}

	// 處理桌機
	@include breakpoint(large) {
		margin-bottom: rem-calc(40);
		padding-left: 0;
		padding-right: 0;
	}
}

// Bottom
.pd-other-info {
	// 處理桌機
	@include breakpoint(large) {
		.page-nav {
			margin-bottom: rem-calc(40);
		}
	}
}

%pdPCStyle {
	@include breakpoint(large) {
		margin-bottom: rem-calc(72);
		padding: 0;
		background: transparent;

		> .grid-container {
			padding: 0;
		}

		&__cont {
			padding: rem-calc(30);
		}
	}
}

@mixin pd-pc-style() {
	@extend %pdPCStyle;
	&__cont {
		@extend %pdPCStyle__cont;
	}

	.sec-title-box {
		@include breakpoint(medium) {
			margin-bottom: rem-calc(20);
		}
	}
}

// --------------------------------------------------------

// pd-banner
.pd-banner {
	$root: &;

	// 處理桌機
	@include breakpoint(medium) {
		width: rem-calc(240);
		height: rem-calc(240);
		border-radius: rem-calc(12);
		box-shadow: $shadow-blue;
		overflow: hidden;

		&[data-layout='null'] {
			width: rem-calc(240);
			height: rem-calc(240);
			box-shadow: none;
			background: transparent;

			#{$root}__cont {
				&::after {
					background: transparent;
				}
			}

			#{$root}__img-box {
				box-shadow: $shadow-blue;
			}

			&::before {
				content: none;
			}
		}

		&[data-layout='3d'] {
			.pd-banner__3d-box {
				width: rem-calc(240);
				height: rem-calc(240);

				// 全螢幕按鈕
				&__btn-full {
					bottom: rem-calc(16);
					right: rem-calc(16);
					left: auto;
				}

				.pd-card__img-tag {
					top: rem-calc(16);
					left: rem-calc(16);
				}
			}
		}
	}
}

// pd-main-info
.pd-main-info {
	&__title {
		color: $bfBlueDark;
	}

	@include breakpoint(medium down) {
		&__bottom-box {
			margin-bottom: rem-calc(22);
		}
	}

	@include breakpoint(small down) {
		&__bottom-box {
			margin-top: 0;
		}
	}

	// 處理桌機
	@include breakpoint(medium) {
		padding-top: rem-calc(5);
		padding-bottom: rem-calc(5);
		padding-left: rem-calc(20);
		&__bottom-box {
			> .grid-container {
				padding: 0;
			}
		}

		> .grid-container {
			padding: 0;
		}
	}

	@include breakpoint(large) {
		padding-top: rem-calc(10);
		padding-bottom: rem-calc(10);

		> .grid-container {
			padding: 0;
		}
	}
}

// pd-detail
.pd-detail {
	// 處理桌機
	@include pd-pc-style;

	@include breakpoint(large) {
	}
}

// pd-history
.pd-history {
	// 處理桌機
	@include pd-pc-style;
}

// pd-ask
.pd-ask {
	// 處理桌機
	@include pd-pc-style;

	@include breakpoint(large) {
		margin-bottom: 0;

		&__cont {
			padding: 0;
		}
	}
}

// pd-list
.pd-list {
	// 處理桌機
	@include pd-pc-style;

	@include breakpoint(large) {
		margin-bottom: rem-calc(50);
	}
}

// --------------------------------------------------------

// PC 右側：large以上show
// 金額
$sidePadding: #{rem-calc(20)};

.count-alert {
	padding: rem-calc(4);
	font-size: rem-calc(15);
	text-align: center;
	color: $white;
	background: primary-gradient-orange(101deg);
	border-radius: rem-calc(6);
}

.pd-side-price {
	$root: &;

	@include pd-sec;

	@include breakpoint(large) {
		padding-top: 0;
		background-color: transparent;

		// Top
		&__top {
			display: flex;
			align-items: baseline;
			justify-content: space-between;
			margin-bottom: rem-calc(24);

			// 數量選擇
			&.count {
				align-items: center;

				#{$root} {
					&__price {
						flex: 1;
						margin-left: rem-calc(14);
					}
				}
			}
		}

		&__note {
			margin-bottom: rem-calc(12);
			font-size: rem-calc(15);
			color: $bfDarkGray;

			em {
				font-style: normal;
				color: $bfSecondaryA;
			}

			// Beanfun! Coin
			&.coin {
				margin-top: rem-calc(-24 + 4);
				margin-bottom: rem-calc(24);
			}
		}

		&__currency {
			color: $bfBlueDark;
			font-weight: $font-bold-m;
		}

		&__price {
			margin-bottom: 0;
			color: $primary-orange;
			font-size: rem-calc(20);
			font-weight: $font-bold-b;
		}

		// cont
		&__cont {
			padding: $sidePadding;
			padding-left: rem-calc(25);
		}

		// btn-box
		&__button-box {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: flex-start;

			.pd-card__img-tag {
				position: relative;
				right: auto;
				top: auto;
				margin-top: 0;
				margin-right: rem-calc(20);
				width: rem-calc(30);
				height: rem-calc(30);

				&::before {
					margin: 0;
				}
			}

			.form {
				width: 100%;
			}

			.btn {
				margin-bottom: 0;
				width: 100%;

				.be-icon {
					margin-right: rem-calc(4);
					font-size: rem-calc(22);
				}
			}
		}

		// 剩餘數量
		&__count-alert {
			margin: rem-calc(-20 -20 20);
			margin-left: rem-calc(-25);
			border-bottom-right-radius: 0;
			border-bottom-left-radius: 0;
		}
	}

	@include breakpoint(xlarge) {
		&__price {
			font-size: rem-calc(28);
		}
	}

	@include breakpoint(xxlarge) {
		&__price {
			font-size: rem-calc(34);
		}
	}
}

// 商品規格
.pd-cont-right {
	.pd-spec {
		@include pd-sec;

		@include breakpoint(large) {
			padding-top: 0;
			background-color: transparent;
			width: 100%;
		}

		&__title {
			display: flex;
			align-items: center;
			justify-content: flex-start;

			.be-icon {
				margin-right: rem-calc(6);
				font-size: rem-calc(12);
			}
		}

		// cont
		&__cont {
			padding: $sidePadding;
		}
	}
}

// 賣家
.pd-side-seller {
	@include breakpoint(large) {
		.seller-info {
			&__l {
				max-width: calc(100% - #{rem-calc(39)});
			}

			// cont
			&__cont {
				padding-left: $sidePadding;
				padding-right: $sidePadding;
			}
		}
		.user-info {
			// item
			&__photo-box {
				margin-right: rem-calc(12);
				width: rem-calc(48);
				height: rem-calc(48);
			}

			&__name {
				font-size: rem-calc(18);
			}
		}
	}
}

