// 使用 Foundation component dropdown 的選單
.dropdown-menu {
	padding: 0;
	width: auto;
	max-width: rem-calc(240);
	border: 0 none;
	border-radius: rem-calc(6);
	box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.16);
	
	&__list {
		margin: 0;
		list-style: none;
	}

	&__item {
		&:not(:last-child) {
			border-bottom: $border-global;
		}
	}

	&__btn {
		display: block;
		padding: rem-calc(18 20);
		font-size: rem-calc(14);
		font-weight: $font-bold-m;
		white-space: nowrap;
		
		&,
		&:hover,
		&:focus {
			color: $primary-dark;
		}

		&.alert {
			
			&,
			&:hover,
			&:focus {
				color: $bfDanger;
			}
		}
	}
}
