// Main Style
@import 'layout/variable_mixin_function.scss';
// --------------------------------------------------------

/* Component */

// --------------------------------------------------------

/* Product Ask Box */
.pd-ask {
	$root: &;

	@include pd-sec;

	&__cont {
		padding: 0;
	}

	&__btn {
		margin-bottom: 0;
		padding: rem-calc(0 7);
		min-width: 0;
		font-size: rem-calc(12);
		line-height: 1.5;
		border-radius: rem-calc(10);

		&.only-text {
			color: $bfDarkGray;
			border: 0;
			padding: 0;
			background-color: transparent;
		}
	}

	// 賣家問答的樣式
	&__ctrl {
		margin-top: rem-calc(10);

		#{$root}-detail & {
			.ask-input {
				padding-left: 0;
				padding-right: 0;
			}
		}

		.ask-input {
			padding-left: 0;
			padding-right: 0;
		}
	}
}

.ask-box {
	$root: &;

	margin: 0;
	list-style: none;

	&__item {
		padding: rem-calc(16);

		&:not(:last-child) {
			border-bottom: $border-global;
		}
	}

	&__info {
		display: flex;
		align-items: center;
		justify-content: space-between;

		&__time {
			font-size: rem-calc(12);
			color: $color-tip-dark;
		}

		&__btn-more {
			display: none;
			color: #999;
			height: rem-calc(21);
		}

		// 頭像（詢問者）
		.user-info {
			&__photo-box {
				@include breakpoint(medium) {
					margin-right: rem-calc(14);
					width: rem-calc(44);
					height: rem-calc(44);
				}
			}
		}
	}

	&__cont {
		padding-left: rem-calc(30);
	}

	&__ans {
		margin-top: rem-calc(15);

		// 頭像（回答者）
		.user-info {
			&__photo-box {
				@include breakpoint(medium) {
					margin-right: rem-calc(12);
					width: rem-calc(32);
					height: rem-calc(32);
				}
			}
		}

		// 內容
		#{$root} {
			&__cont {
				padding-left: rem-calc(24 + 6);

				@include breakpoint(medium) {
					padding-left: rem-calc(32 + 12);
				}
			}
		}

		// 賣家-問與答
		&.seller-ans {
			#{$root}__cont {
				display: flex;
				align-items: center;
				justify-content: space-between;
			}

			#{$root}__desc {
				flex: 1;
			}

			#{$root}__btn {
				flex-shrink: 0;
			}
		}
	}

	.user-info {
		&__name {
			font-size: rem-calc(13);
		}

		.be-icon {
			color: #999;
			margin-left: rem-calc(6);
		}
	}

	@include breakpoint(medium) {
		font-size: rem-calc(18);

		&__item {
			padding: rem-calc(30);
		}

		&__info {
			&__time {
				font-size: rem-calc(14);
			}
		}

		&__cont {
			padding-left: rem-calc(44 + 14);
		}

		.user-info {
			&__name {
				font-size: rem-calc(15);
			}
		}
	}
}

/* Product Ask Input */
.ask-input {
	padding: rem-calc(16);

	&__input-box {
		position: relative;
	}

	// <textarea>
	&__input-area {
		margin-bottom: 0;
		padding: rem-calc(8 12);
		padding-right: rem-calc(90);
		height: rem-calc(36);
		min-height: 0;
		font-size: rem-calc(15);
		line-height: 1.2;
		background-color: #f4f4f4;
		border-color: transparent;
		border-radius: rem-calc(18);
		box-shadow: none;
		resize: none;

		&:focus {
			border-color: #ccc;
			box-shadow: none;
		}
	}

	// 固定的項目
	&__fixed-box {
		position: absolute;
		top: 0;
		right: 0%;
		display: flex;
		align-items: center;
		padding-right: rem-calc(12);
		height: 100%;
		color: $color-tip;
	}

	// 字數限制提醒
	&__limit {
		margin-right: rem-calc(8);
		font-size: rem-calc(13);
	}

	// 送出按鈕
	&__btn-submit {
		color: $bfDarkGray;

		&:disabled,
		&[disabled] {
			color: $bfLightGray;
		}
	}
}
