/* 使用者資訊（頭像 + 名字） */
.user-info {
	display: flex;
	align-items: center;
	justify-content: center;

	&__photo-box {
		margin-right: rem-calc(6);
		width: rem-calc(24);
		height: rem-calc(24);
		background: center center / cover;
		border-radius: 99px;
	}

	&__name {
		font-size: rem-calc(16);
		font-weight: 500;
	}
}
